<template>
  <view class="record-page">
    <!-- Áä∂ÊÄÅÊòæÁ§∫ -->
    <view class="status-card">
      <text class="status-text">{{ statusText }}</text>

      <view class="time-display" v-if="isRecording || hasRecord">
        {{ formattedTime }}
      </view>
    </view>

    <!-- ÊéßÂà∂ÊåâÈíÆ -->
    <view class="control-buttons">
      <!-- ÂºÄÂßãÂΩïÈü≥ -->
      <button
          class="record-btn start-btn"
          v-if="!isRecording && !hasRecord"
          @tap="handleStartRecord"
          :loading="isStarting"
      >
        ÂºÄÂßãÂΩïÈü≥
      </button>

      <!-- ÂÅúÊ≠¢ÂΩïÈü≥ -->
      <button
          class="record-btn stop-btn"
          v-if="isRecording"
          @tap="handleStopRecord"
      >
        ÂÅúÊ≠¢ÂΩïÈü≥
      </button>

      <!-- ÈáçÊñ∞ÂΩïÈü≥ -->
      <button
          class="record-btn redo-btn"
          v-if="!isRecording && hasRecord"
          @tap="handleRedoRecord"
      >
        ÈáçÊñ∞ÂΩïÈü≥
      </button>
    </view>

    <!-- ÂäüËÉΩÂå∫Âüü -->
    <view class="function-section" v-if="hasRecord && !isRecording">
      <!-- Êí≠ÊîæÊéßÂà∂ -->
      <view class="play-control">
        <button class="play-btn" @tap="handlePlay">
          {{ isPlaying ? 'ÊöÇÂÅúÊí≠Êîæ' : 'Êí≠ÊîæÂΩïÈü≥' }}
        </button>
      </view>
    </view>

    <!-- ‰∏ä‰º† -->
    <view class="upload-section" v-if="hasRecord && !isRecording">
      <button
          class="upload-btn"
          @tap="handleUpload"
          :loading="isUploading"
          :disabled="isUploading"
      >
        {{ isUploading ? '‰∏ä‰º†‰∏≠...' : '‰∏ä‰º†ÂΩïÈü≥' }}
      </button>

      <text class="upload-tip" v-if="uploadResult">
        {{ uploadResult }}
      </text>
    </view>

    <!-- Ë∞ÉËØï‰ø°ÊÅØÔºàÂºÄÂèëÊó∂ÂèØËßÅÔºâ -->
    <view class="debug-info" v-if="isDev">
      <text>Êñá‰ª∂Ë∑ØÂæÑ: {{ filePath || 'Êó†' }}</text>
      <text>ÂΩïÈü≥Êó∂Èïø: {{ recordTime }}Áßí</text>
      <text>ÂΩïÈü≥Áä∂ÊÄÅ: {{ isRecording ? 'ÂΩïÈü≥‰∏≠' : 'Êú™ÂΩïÈü≥' }}</text>
    </view>
  </view>
</template>

<script>
// ÂØºÂÖ•ÂΩïÈü≥ÁÆ°ÁêÜÂô®
import recordManager from '@/utils/record-manager.js'

export default {
  data() {
    return {
      isRecording: false,      // ÊòØÂê¶Ê≠£Âú®ÂΩïÈü≥
      hasRecord: false,        // ÊòØÂê¶ÊúâÂΩïÈü≥Êñá‰ª∂
      isPlaying: false,        // ÊòØÂê¶Ê≠£Âú®Êí≠Êîæ
      isUploading: false,      // ÊòØÂê¶Ê≠£Âú®‰∏ä‰º†
      isStarting: false,       // ÂºÄÂßãÂΩïÈü≥‰∏≠
      recordTime: 0,           // ÂΩïÈü≥Êó∂Èïø
      filePath: '',            // Êñá‰ª∂Ë∑ØÂæÑ
      uploadResult: '',        // ‰∏ä‰º†ÁªìÊûú
      isDev: true,             // ÂºÄÂèëÊ®°Âºè
      innerAudioContext: null  // Èü≥È¢ëÊí≠Êîæ‰∏ä‰∏ãÊñá
    }
  },

  computed: {
    // Áä∂ÊÄÅÊñáÊú¨
    statusText() {
      if (this.isRecording) return 'üé§ ÂΩïÈü≥‰∏≠...'
      if (this.hasRecord) return '‚úÖ ÂΩïÈü≥ÂÆåÊàê'
      return 'üì± ÂáÜÂ§áÂΩïÈü≥'
    },

    // Ê†ºÂºèÂåñÊó∂Èó¥
    formattedTime() {
      const minutes = Math.floor(this.recordTime / 60)
      const seconds = this.recordTime % 60
      return `${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`
    }
  },

  onLoad() {
    console.log('È°µÈù¢Âä†ËΩΩ')
    // ÂàùÂßãÂåñÂΩïÈü≥ÁÆ°ÁêÜÂô®
    recordManager.init()
    recordManager.setTimeUpdateCallback((seconds, formatted) => {
      // Âè™Âú®ÂΩïÈü≥‰∏≠ÊâçÊõ¥Êñ∞È°µÈù¢ÊòæÁ§∫ÔºåÁ°Æ‰øùÁä∂ÊÄÅÂêåÊ≠•
      if (this.isRecording || recordManager.isRecording) {
        this.recordTime = seconds
        // Âº∫Âà∂Êõ¥Êñ∞ËßÜÂõæ
        this.$forceUpdate()
      }
    })

    // ÂàùÂßãÂåñÈü≥È¢ëÊí≠Êîæ‰∏ä‰∏ãÊñá
    this.initInnerAudioContext()

  },

  onUnload() {
    // Ê∏ÖÁêÜËµÑÊ∫ê
    recordManager.destroy()
    // ÈîÄÊØÅÈü≥È¢ë‰∏ä‰∏ãÊñá
    if (this.innerAudioContext) {
      this.innerAudioContext.destroy()
    }
  },

  methods: {
    // ÂàùÂßãÂåñÈü≥È¢ëÊí≠Êîæ‰∏ä‰∏ãÊñá
    initInnerAudioContext() {
      console.log('ÂàùÂßãÂåñÈü≥È¢ëÊí≠Êîæ‰∏ä‰∏ãÊñá')
      this.innerAudioContext = uni.createInnerAudioContext()
      
      // ÈÖçÁΩÆÈü≥È¢ë
      this.innerAudioContext.autoplay = false
      this.innerAudioContext.loop = false
      this.innerAudioContext.volume = 1.0
      
      // ÁõëÂê¨ÂêÑÁßç‰∫ã‰ª∂
      this.innerAudioContext.onCanplay(() => {
        console.log('Èü≥È¢ëÂèØ‰ª•Êí≠Êîæ‰∫Ü')
      })
      
      this.innerAudioContext.onPlay(() => {
        console.log('Èü≥È¢ëÂºÄÂßãÊí≠Êîæ')
      })
      
      this.innerAudioContext.onPause(() => {
        console.log('Èü≥È¢ëÊöÇÂÅú')
      })
      
      this.innerAudioContext.onStop(() => {
        console.log('Èü≥È¢ëÂÅúÊ≠¢')
      })
      
      // ÁõëÂê¨Êí≠ÊîæÁªìÊùü
      this.innerAudioContext.onEnded(() => {
        console.log('Êí≠ÊîæÂÆåÊàê')
        this.isPlaying = false
      })
      
      // ÁõëÂê¨Êí≠ÊîæÈîôËØØ
      this.innerAudioContext.onError((err) => {
        console.error('Êí≠ÊîæÂ§±Ë¥•:', err)
        uni.showToast({
          title: 'Êí≠ÊîæÂ§±Ë¥•: ' + (err.errMsg || err.message),
          icon: 'none'
        })
        this.isPlaying = false
      })
      
      this.innerAudioContext.onWaiting(() => {
        console.log('Èü≥È¢ëÂä†ËΩΩ‰∏≠...')
      })
      
      this.innerAudioContext.onSeeking(() => {
        console.log('Èü≥È¢ëË∑≥ËΩ¨‰∏≠...')
      })
      
      this.innerAudioContext.onSeeked(() => {
        console.log('Èü≥È¢ëË∑≥ËΩ¨ÂÆåÊàê')
      })
    },
    // ÂºÄÂßãÂΩïÈü≥
    async handleStartRecord() {
      console.log('ÁÇπÂáªÂºÄÂßãÂΩïÈü≥')
      this.isStarting = true

      try {
        await recordManager.startRecord()
        this.isRecording = true
        this.hasRecord = false
        this.uploadResult = ''
        // Á´ãÂç≥ÂêåÊ≠•ËÆ°Êó∂ÊòæÁ§∫ÔºåÁ°Æ‰øù‰ªé0ÂºÄÂßã
        this.recordTime = recordManager.recordTime

        uni.showToast({
          title: 'ÂºÄÂßãÂΩïÈü≥',
          icon: 'none',
          duration: 1500
        })
      } catch (error) {
        console.error('ÂºÄÂßãÂΩïÈü≥Â§±Ë¥•:', error)
        uni.showToast({
          title: 'ÂºÄÂßãÂΩïÈü≥Â§±Ë¥•',
          icon: 'none'
        })
      } finally {
        this.isStarting = false
      }
    },

    // ÂÅúÊ≠¢ÂΩïÈü≥
    async handleStopRecord() {
      console.log('ÁÇπÂáªÂÅúÊ≠¢ÂΩïÈü≥')
      await recordManager.stopRecord()
      this.isRecording = false
      this.hasRecord = true
      this.filePath = recordManager.getRecordFile()

      uni.showToast({
        title: 'ÂΩïÈü≥ÂÆåÊàê',
        icon: 'success'
      })

      console.log('ÂΩïÈü≥Êñá‰ª∂Ë∑ØÂæÑ:', this.filePath)
    },

    // ÈáçÊñ∞ÂΩïÈü≥
    handleRedoRecord() {
      uni.showModal({
        title: 'ÊèêÁ§∫',
        content: 'Á°ÆÂÆöË¶ÅÈáçÊñ∞ÂΩïÈü≥ÂêóÔºü',
        success: (res) => {
          if (res.confirm) {
            this.isRecording = false
            this.hasRecord = false
            this.isPlaying = false
            this.recordTime = 0
            this.uploadResult = ''
            this.filePath = ''
            recordManager.tempFilePath = ''
            recordManager.recordTime = 0
          }
        }
      })
    },

    // Êí≠ÊîæÂΩïÈü≥
    handlePlay() {
      console.log('ÁÇπÂáªÊí≠ÊîæÂΩïÈü≥')
      if (!this.filePath) {
        uni.showToast({
          title: 'ËØ∑ÂÖàÂΩïÈü≥',
          icon: 'none'
        })
        console.log('Êí≠ÊîæÂ§±Ë¥•ÔºöfilePath‰∏∫Á©∫')
        return
      }

      console.log('ÂΩìÂâçfilePath:', this.filePath)
      console.log('ÂΩìÂâçisPlaying:', this.isPlaying)
      console.log('innerAudioContext:', this.innerAudioContext)

      if (this.isPlaying) {
        // ÊöÇÂÅúÊí≠Êîæ
        console.log('ÊöÇÂÅúÊí≠Êîæ')
        this.innerAudioContext.pause()
        this.isPlaying = false
      } else {
        // ÂºÄÂßãÊí≠Êîæ
        console.log('ÂºÄÂßãÊí≠ÊîæÔºåËÆæÁΩÆsrc:', this.filePath)
        this.innerAudioContext.src = this.filePath
        
        // Áõ¥Êé•Ë∞ÉÁî®play()ÔºåÂæÆ‰ø°Â∞èÁ®ãÂ∫è‰∏≠‰∏çÈúÄË¶Åload()ÊñπÊ≥ï
        console.log('Ë∞ÉÁî®play()')
        this.innerAudioContext.play()
        
        this.isPlaying = true
        console.log('ËÆæÁΩÆisPlaying‰∏∫true')
      }
    },

    // ‰∏ä‰º†ÂΩïÈü≥ÔºàÊ®°Êãü‰∏ä‰º†Ôºâ
    async handleUpload() {
      if (!this.filePath) {
        uni.showToast({
          title: 'ËØ∑ÂÖàÂΩïÈü≥',
          icon: 'none'
        })
        return
      }

      this.isUploading = true
      this.uploadResult = ''

      try{
        uni.showToast({
          title: 'ÂºÄÂßã‰∏ä‰º†ÂΩïÈü≥',
          mask: true
        })
        //‰ΩøÁî®uni.uploadFile API
        const uploadTask = uni.uploadFile({
          url: 'http://localhost:3000/api/upload',
          filePath: this.filePath,
          name: 'record-backend',
          formData: {
            userId: 'test_user_001',
            timestamp: Date.now(),
            duration: this.recordTime
          },
          success: (uploadRes) => {
            const result = JSON.parse(uploadRes.data)
            if (result.success){
              uni.showToast({
                title: '‰∏ä‰º†ÊàêÂäü',
                icon: 'success'
              })
              this.uploadResult =
                  `‚úÖ ‰∏ä‰º†ÊàêÂäüÔºÅ
                  Êñá‰ª∂Âêç: ${result.data.filename}
                  Â§ßÂ∞è: ${this.formatFileSize(result.data.size)}
                  Êó∂Èó¥: ${new Date().toLocaleTimeString()}`
              console.log('‰∏ä‰º†ÊàêÂäü',result.data)
            } else {
              throw  new Error(result.message || '‰∏ä‰º†Â§±Ë¥•')
            }
          },
          fail: (err) => {
            console.error('‰∏ä‰º†Â§±Ë¥•',err)
            throw new Error(`‰∏ä‰º†ËØ∑Ê±ÇÂ§±Ë¥•: ${err.errMsg || err.message}`)
          },
          complete: () => {
            uni.hideLoading()
            this.isUploading = false
          }
        })
        uploadTask.onProgressUpdate((res) => {
          console.log('‰∏ä‰º†ËøõÂ∫¶:', res.progress + '%')
          // ÂèØ‰ª•Âú®ËøôÈáåÊòæÁ§∫ËøõÂ∫¶Êù°
        })
      } catch ( error){
        console.error('‰∏ä‰º†Âá∫Èîô:', error)
        uni.showToast({
          title: `‰∏ä‰º†Â§±Ë¥•: ${error.message}`,
          icon: 'none',
          duration: 3000
        })
        this.uploadResult = `‰∏ä‰º†Â§±Ë¥•: ${error.message}`
        this.isUploading = false
      }
    },
    formatFileSize(bytes) {
      if (bytes === 0) return '0 B'
      const k = 1024
      const sizes = ['B', 'KB', 'MB', 'GB']
      const i = Math.floor(Math.log(bytes) / Math.log(k))
      return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
    }
  }
}
</script>

